import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'

import Bio from '../../components/Bio'

class BlogIndex extends React.Component {
  render() {
    const siteTitle = get(this, 'props.data.site.siteMetadata.title')
    const posts = get(this, 'props.data.allMarkdownRemark.edges')

    console.log('posts', posts)

    return (
      <div className='blog-map'>
        <Helmet title={get(this, 'props.data.site.siteMetadata.title')} />

        {typeof posts === 'undefined'
          ? <p className = 'filler'>COMING SOON</p>
          : posts.map(post => {
              if (post.node.path !== '/404/') {
                const title = get(post, 'node.frontmatter.title') || post.node.path
                return (
                  <div key={post.node.frontmatter.path} className = 'blog-item'>
                    <h1 >
                      <Link
                        style={{ boxShadow: 'none' }}
                        to={post.node.frontmatter.path}
                        className = 'blog-title'
                      >
                        {post.node.frontmatter.title}
                      </Link>
                    </h1>
                    <div> {post.node.frontmatter.image}</div>
                    <small className='blog-map-date'>{post.node.frontmatter.date}</small>
                    <p dangerouslySetInnerHTML={{ __html: post.node.excerpt }} />
                  </div>
                )
              }
            })}
          </div>
    )
  }
}

BlogIndex.propTypes = {
  route: React.PropTypes.object,
}

export default BlogIndex

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          frontmatter {
            path
            date(formatString: "DD MMMM, YYYY")
          }
          frontmatter {
            title
          }
        }
      }
    }
  }
`



